</template>

<script>
import AXIOS from '../axios/axios'
const Axios = new AXIOS();
export default {
  name: 'address',
  data () {
    return {
      limitNum: 3,
      addressList: [],
      curIndex: 0,
      sendMethod: 1,
      curAddress: '',
      isDelete: false
    }
  },
  mounted (){
    this.$nextTick( ()=>{
      this.getAddressList();
    });
  },
  methods:{
    getAddressList(){
      this.$http.get('../static/axios/address.json')
        .then( res =>{
          if( res.data.status == '0' ){
            this.addressList = res.data.result;
          }
        })
        .catch( res =>{
          console.log(res);
        });
    },
    setDefault(addressId){
      this.addressList.forEach((address,index) =>{
        if( address.addressId == addressId ){
          address.isDefault = true;
        }else{
          address.isDefault = false;
        }
      });
    },
    addProduct(){
      this.addressList.push({
        "addressId":"100006",
        "userName":"JackBean",
        "streetName":"北京市朝阳区朝阳公园",
        "postCode":"100001",
        "tel":"12345678901",
        "isDefault":false
      });
      
    },

    deleteConfirm(address){
      this.isDelete = true;
      this.curAddress = address;
    },
    deleteAddress(){
      let index = this.addressList.indexOf(this.curAddress);
      this.addressList.splice(index,1);
      this.isDelete = false;
    }
  },
  computed:{
    filterAddress(){
      return this.addressList.slice(0,this.limitNum);
    }
  }
}
</script>

